import React from 'react';
import axios from 'axios';
import { Redirect } from 'react-router-dom';
import Image from 'react-graceful-image';
import '../App.css';
import LoadingIndicator from '../components/LoadingIndicator';

class UserImages extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			imageURLs: [],
			isLoading: true
		}
	}

	keyExists() {

	}
	
	componentDidMount() {

		if ( localStorage.hasOwnProperty('auth-key') ) {
			axios( {
				method: 'GET',
				url: 'https://insta.nextacademy.com/api/v1/images/me',
				headers: {
					'Authorization': 'Bearer ' + localStorage.getItem('auth-key')
				}
			} )
			.then( result => {
				this.isLoading = false;
				this.setState({imageURLs: result.data});
			} )
			.catch( error => {
				alert('OMG SOMETHING WENT WRONG CHECK CONSOLE');
				console.log(error.response);
			} )
		} else {
			alert('Couldn\'t load your images, because you\'re not logged in.');
		}
	}

	render() {
		return (
			<React.Fragment>
				{
					localStorage.hasOwnProperty('auth-key') ?
					<div className='container my-3 '>
					{
						this.isLoading === false ?
							<React.Fragment>
								<div className='row user-summary-card w-100 d-flex align-items-center'>
									<Image src={localStorage.getItem('userProfileImageURL')} className="profile-image" />
									<span className='user-name-text'> {localStorage.getItem('username')}</span> <span className='user-id-text'>#{localStorage.getItem('userID')}</span>
								</div>

								<div className='row w-100 user-image-section d-flex flex-wrap'>
									{ this.state.imageURLs.map(imageURL => 
										<Image key={Math.random() + imageURL} src={imageURL} className="user-image" alt={imageURL} />
									) }
								</div>
							</React.Fragment>
						:
							<div className='row d-flex align-items-center justify-content-center w-100'>
								<LoadingIndicator size='100' />
							</div>
					}
				</div>
					:
						<Redirect to='/' />
				}

		</React.Fragment>
		)
	}
}

export default UserImages