import React from 'react';
import '../App.css';
import { Link } from 'react-router-dom';
import LoginModal from '../components/LoginModal';

class NavBar extends React.PureComponent {
	
	constructor(props) {
		super(props);
		this.state = {
			loginModalState: false,
			userLoggedIn: this.checkAuthKeyPresent()
		}
	}

	showLoginModal = event => {
		let modalContainer = document.querySelector('#login-form-container');
		let modal = document.querySelector('#login-form-modal');
		modal.classList.remove('anim-slide-out-top');
		modalContainer.classList.remove('anim-slide-out-top');
		modal.classList.add('anim-slide-in-top');
		modalContainer.classList.add('anim-slide-in-top');
	}

	logOut = event => {
		localStorage.removeItem('auth-key');
		localStorage.removeItem('user');
		localStorage.clear();
		this.setState({userLoggedIn: this.checkAuthKeyPresent()});
		alert('User logged out!');
	}

	checkAuthKeyPresent() {
		console.log(localStorage.getItem('auth-key'));
		if (localStorage.hasOwnProperty('auth-key')) {
			console.log('found auth key:' + localStorage.getItem('auth-key'));
			return true;
		} else {
			console.log('no auth key found');
			return false;
		}
	}

	handleLoginSpawn = event => {

	}

	render() {
		return (
			<React.Fragment>
				<nav className='row d-flex justify-content-center align-items-center align-content-center py-3 '>
					<ul>
						<Link to='/'> <button> <li>  Home </li> </button> </Link>
						{/* <Link to='/#/users/1'> <button> <li> User Profile Page </li> </button> </Link> */}
						{
							this.state.userLoggedIn ?
								<React.Fragment>
									<Link to='/profile'> <button> <li> My Profile </li> </button> </Link>
									<Link to='/'><button onClick={this.logOut}> <li > Log Out </li> </button></Link>
								</React.Fragment>
							:
								<button onClick={this.showLoginModal}> <li > Login </li> </button>
						}
					</ul>
		
					<LoginModal id='login-modal' parentNav={this}  />
					{
						this.state.loginModalState === true ? 
							null
						:
							null
					}


					{/* <Route path='/login/:formType' component={LoginModal} /> */}
				</nav>
			</React.Fragment>
		);
	}
}

export default NavBar;