import React from 'react';
import '../App.css';
import Axios from 'axios';

class LoginForm extends React.PureComponent {

	constructor(props) {
		super(props);
		this.state = {
			email: '',
			password: '',
			submitButtonState: false,
			parentModal: props.parentModal
		}
	}
	
	handleInput = event => {
		if (event.target.id === 'login-email-input') {
			this.setState({ email: event.target.value })
		} else if (event.target.id === 'login-password-input') {
			this.setState({ password: event.target.value })
		}
		
		if ( this.checkEmailValidity( this.state.email ) && this.checkAllStatesPopulated() ) {
			this.setState({submitButtonState: true});
		} else {
			this.setState({submitButtonState: false});
		}

	}

	handleSubmit = event => {
		event.preventDefault();
		console.log('Submitted, Email: ' + this.state.email + ', Password: ' + this.state.password);

		if ( this.checkAllStatesPopulated() && this.checkEmailValidity( this.state.email ) ) {
			Axios( {
				method: 'POST',
				url: 'https://insta.nextacademy.com/api/v1/login',
				headers: {
					'content-type': 'application/json'
				},
				data: {
					email: this.state.email,
					password: this.state.password
				}
			} )
			.then( response => {
				console.log(response);
				localStorage.setItem('auth-key', response.data.auth_token);
				localStorage.setItem('userID', response.data.user.id);
				localStorage.setItem('username', response.data.user.username);
				localStorage.setItem('userProfileImageURL', response.data.user.profile_picture);
				console.log(localStorage.getItem('auth-key'));
				this.state.parentModal.state.parentNav.setState({userLoggedIn: true});
				this.state.parentModal.handleDismiss();
				alert('Successfully logged in!');
			})
			.catch( error => {
				console.log(error);
				alert('Couldn\'t log in, something broke!');
			})
		} else {
			alert('o no somethign went wrong :o');
		}

	}

	checkAllStatesPopulated() {
		let allStatesPopulated = true;
		if (	
			this.state.email !== '' &&
			this.state.password !== ''
		) {
			allStatesPopulated = true;
		} else {
			allStatesPopulated = false;
		}
		return allStatesPopulated;
	}

	checkEmailValidity( emailString ) {
		let validityRegEx = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		return validityRegEx.test( String(emailString).toLowerCase() );
	}

	render() {
		return (
			<React.Fragment>
				<div className="modal-header">
					<h3 className=''> Log In </h3>
				</div>

				<div className="modal-body">
					<form>		
						<div className='form-group'>
							<label htmlFor='login-email-input'>Email</label>
							<input id='login-email-input' type='email' className='form-control' placeholder='Email' onChange={this.handleInput} />
						</div>
						<div className='form-group'>
							<label htmlFor='login-password-input'>Password</label>
							<input id='login-password-input' type='password' className='form-control' placeholder='Password' onChange={this.handleInput} />
						</div>
					</form>

					<p>Don't have an account? <button className='text-button' onClick={this.state.parentModal.switchToSignupMode} > Sign up here. </button></p>

				</div>

				<div className="modal-footer">
					<button id='btn-login-submit' className='btn btn-lg btn-primary' onClick={this.handleSubmit} disabled={!this.state.submitButtonState}> Login </button>
					<button className='btn btn-lg btn-secondary' onClick={this.state.parentModal.handleDismiss} > Cancel </button>
				</div>

			</React.Fragment>

		);
	}

}

export default LoginForm;