import React from 'react';
import Image from 'react-graceful-image';
import '../App.css';

const loadingImage = '../Blocks-1s-200px.svg'

const LoadingIndicator = (props) => {
	return (
		<Image src={loadingImage} alt='Loading' width={props.size} height={props.size} />
	);
}

export default LoadingIndicator