import React from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import Image from 'react-graceful-image';
import '../App.css';
import LoadingIndicator from '../components/LoadingIndicator';

class UserImages extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			userID: props.userID,
			user: '',
			imageURLs: [],
			isLoading: true
		}
	}

	
	componentDidMount() {
		axios.get('https://insta.nextacademy.com/api/v1/users')
		.then( result => {
			let targetUser = result.data.find(user => user.id === this.state.userID);
			this.setState({user: targetUser});
		} )


		axios.get('https://insta.nextacademy.com/api/v1/images?userId=' + this.state.userID)
		.then( result => {
			this.isLoading = false;
			this.setState({imageURLs: result.data});
		} )
	}

	render() {
		return (
			<React.Fragment>
				<div className='container my-3 '>
					{
						this.isLoading === false ?
							<React.Fragment>
								<div className='row user-summary-card w-100 d-flex align-items-center'>
									<Image src={this.state.user.profileImage} className="profile-image" />
									<Link to={ '/users/' + this.state.userID }>
										<span className='user-name-text'> {this.state.user.username}</span> <span className='user-id-text'>#{this.state.user.id}</span>
									</Link>
								</div>

								<div className='row w-100 user-image-section d-flex flex-wrap'>
									{ this.state.imageURLs.map(imageURL => 
										<Image key={this.state.userID + imageURL + Math.random()} src={imageURL} className="user-image" alt={imageURL} />
									) }
								</div>
							</React.Fragment>
						:
							<div className='row d-flex align-items-center justify-content-center w-100'>
								<LoadingIndicator size='100' />
							</div>
					}
				</div>
		</React.Fragment>
		)
	}
}

export default UserImages