import React from 'react';
import '../App.css';
import axios from 'axios';


class LoginModal extends React.PureComponent {

	constructor(props) {
		super(props);
		this.state = {
			username: '',
			email: '',
			password: '',
			passwordConfirm: '',
			submitButtonState: false,
			parentModal: props.parentModal
		}
	}

	handleInput = event => {
		if (event.target.id === 'signup-email-input') {
			this.setState({ email: event.target.value })
		} else if (event.target.id === 'signup-password-input') {
			this.setState({ password: event.target.value })
		} else if (event.target.id === 'signup-passwordconfirmation-input') {
			this.setState({ passwordConfirm: event.target.value })
		} else if (event.target.id === 'signup-username-input') {
			this.setState({ username: event.target.value })
		}

		if ( this.checkAllStatesPopulated() && this.checkEmailValidity( this.state.email ) ) {
			this.setState({submitButtonState: true })
		} else {
			this.setState({submitButtonState: false })
		}
	}

	handleSubmit = event => {
		event.preventDefault();

		let allStatesPopulated = this.checkAllStatesPopulated();
		let passwordsMatch = this.checkPasswordsMatch();
		let emailValid = this.checkEmailValidity( this.state.email );
		
		let alertString = 'Could not submit - you need to address the following problems: \n';
		if (!allStatesPopulated) { alertString += '- All fields need to be filled before submitting. \n'; }
		if (!passwordsMatch) { alertString += '- Confirm your password again, dummy, they don\'t match \n'; }
		if (!emailValid) { alertString += '- Check your email address format again. \n'; }

		if (allStatesPopulated && passwordsMatch && emailValid) {
			console.log(this.state);
			axios( {
				method: 'POST',
				url: 'https://insta.nextacademy.com/api/v1/users/new', 
				data: {
					username: this.state.username,
					email: this.state.email,
					password: this.state.password
				} 
			} ) 
			.then(response => {
				console.log(response)
				alert(response.data.message);
				localStorage.setItem('auth-key', response.data.auth_token);
				localStorage.setItem('userID', response.data.user.id);
				localStorage.setItem('username', response.data.user.username);
				localStorage.setItem('userProfileImageURL', response.data.user.profile_picture);
				console.log(localStorage.getItem('auth-key'));
			} )
			.catch( error => {
				console.log(error.response.data);
				let errorString = 'Couldn\'t sign you up, because: \n'
				error.response.data.message.forEach( message => {
					errorString += message + '\n';
				})
				alert(errorString);
			} ) ;

		} else {
			alert(alertString);
		}

	}

	checkPasswordsMatch() {
		let passwordsMatch = true;
		if (this.state.password === this.state.passwordConfirm) {
			passwordsMatch = true;
		} else {
			passwordsMatch = false;
		}
		return passwordsMatch;
	}

	checkAllStatesPopulated() {
		let allStatesPopulated = true;
		if (	
			this.state.username !== '' &&
			this.state.username.length >= 5 &&
			this.state.username.length <= 20 &&
			this.state.password.length >= 8 &&
			this.state.password.length <= 50 &&
			this.state.email !== '' &&
			this.state.password !== '' &&
			this.state.passwordConfirm !== ''
		) {
			allStatesPopulated = true;
		} else {
			allStatesPopulated = false;
		}
		return allStatesPopulated;
	}

	checkEmailValidity( emailString ) {
		let validityRegEx = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
		return validityRegEx.test( String(emailString).toLowerCase() );
	}

	render() {
		return (
			<React.Fragment>
				<div className="modal-header">
					<h3 className=''> Log In </h3>
				</div>

				<div className="modal-body">
					<form>
						<div className='form-group'>
							<label htmlFor='signup-username-input'>Username</label>
							<input id='signup-username-input' type='text' className='form-control' placeholder='Username' onChange={this.handleInput} />
						</div>
						<div className='form-group'>
							<label htmlFor='signup-email-input'>Email</label>
							<input id='signup-email-input' type='email' className='form-control' placeholder='Email' onChange={this.handleInput} />
						</div>
						<div className='form-group'>
							<label htmlFor='signup-password-input'>Password</label>
							<input id='signup-password-input' type='password' className='form-control' placeholder='Password' onChange={this.handleInput} />
						</div>
						<div className='form-group'>
							<label htmlFor='signup-passwordconfirmation-input'>Confirm Password</label>
							<input id='signup-passwordconfirmation-input' type='password' className='form-control' placeholder='Confirm Password' onChange={this.handleInput} />
						</div>
						</form>

						<p>Already have an account? <button className='text-button' onClick={this.state.parentModal.switchToLoginMode} > Log in here. </button></p>

					</div>

					<div className="modal-footer">
						<button id='btn-signup-submit' ref='btnSignupSubmit' className='btn btn-lg btn-primary' onClick={this.handleSubmit} disabled={!this.state.submitButtonState}> Sign Up </button>
						<button className='btn btn-lg btn-secondary' onClick={this.state.parentModal.handleDismiss} > Cancel </button>
					</div>

			</React.Fragment>
		);
	}

}

export default LoginModal;