import React from 'react';
import '../App.css';
import LoginForm from './LoginForm';
import SignupForm from './SignupForm';


class LoginModal extends React.PureComponent {

	constructor(props) {
		super(props);
		this.state = {
			signupMode: false,
			parentNav: props.parentNav
		}
	}

	handleDismiss = event => {
		document.querySelector('#login-form-container').classList.add('anim-slide-out-top');
		document.querySelector('#login-form-modal').classList.add('anim-slide-out-top');
		document.querySelector('#login-form-container').classList.remove('anim-slide-in-top');
		document.querySelector('#login-form-modal').classList.remove('anim-slide-in-top');
	}

	switchToSignupMode = event => {
		this.setState({signupMode: true})
	}
	
	switchToLoginMode = event => {
		this.setState({signupMode: false})
	}

	render() {
		return (
			<div id='login-form-container' className='container-fluid h-100 login-form-container d-flex justify-content-center align-items-center' >
				<div id='login-form-modal' className="modal-dialog login-form">
					<div className="modal-content px-3 py-3">

							{
								this.state.signupMode === true ? 
									<SignupForm parentModal={this} />	
								:
									<LoginForm parentModal={this}/>
							}

					</div>
				</div>
			</div>
		);
	}

}

export default LoginModal;