import React from 'react';
import '../App.css';
import NavBar from '../components/NavBar';
import UserSummary from '../containers/UserSummary';
import UserProfilePage from '../pages/UserProfilePage';
import OwnUserProfilePage from '../pages/OwnUserProfilePage';
import { Route } from 'react-router-dom';

const HomePage = (props) => {
	return (
		<React.Fragment>
			<div className='container-fluid'>
				<h1 className='display-1'>DelayOunce</h1>
				<p className='lead'>Like Instagram, but slower and in imperial units.</p>
				<NavBar  />
				<Route exact path='/' component={UserSummary} />
				<Route path='/profile' component={OwnUserProfilePage} />
				<Route exact path='/users/:id' component={UserProfilePage} />
			</div>
	</React.Fragment>
	)
}

export default HomePage